import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import _ from "lodash";

import SearchBar from "../src/components/SearchBar";
import { fetchByUsername, getProfile } from "../src/actions";
import {
  Container,
  Grid,
  Segment,
  Icon,
  Header,
  Label,
  Menu,
  Table
} from "semantic-ui-react";

// show awards, i.e. most kills, most wins, highest kd etc etc in separate div

class Leaderboard extends Component {
  constructor(props) {
    super(props);

    this.state = { players: [], mode: "SOLO" };
  }

  handleRouteChange = async username => {
    const { players } = this.state;
    const newPlayer = await getProfile(username);
    this.setState({ players: [...players, newPlayer] }, () =>
      console.log(this.state.players)
    );
    const mode = "solo";
    console.log(newPlayer.stats[`kd_${mode}`]);
    // need to reset seach component after fetch
  };

  prefetchRoute = username => {
    this.props.router.prefetch(`/profile/${username}`);
  };

  render() {
    const { searchResults, fetchByUsername } = this.props;

    const debouncedfetchByUsername = _.debounce(fetchByUsername, 500, {
      maxWait: 1000
    });

    return (
      <React.Fragment>
        <Container
          textAlign="center"
          style={{
            paddingTop: "200px",
            paddingBottom: "100px",
            height: "100%"
          }}
        >
          <h1>Leaderboard</h1>
          <Grid columns="1" centered textAlign="center">
            <Grid.Column
              mobile="16"
              computer="6"
              largeScreen="6"
              tablet="6"
              widescreen="6"
            >
              <Container>
                <SearchBar
                  handleRouteChange={this.handleRouteChange}
                  prefetchRoute={this.prefetchRoute}
                  fetchByUsername={debouncedfetchByUsername}
                  results={searchResults}
                />
              </Container>
            </Grid.Column>
            <Grid.Column textAlign="center">
              <Container text style={{ marginTop: "1em" }}>
                {/* ******************* */}
                <Grid style={{ backgroundColor: "#2c3c57", color: "white" }}>
                  <Grid.Row style={{ backgroundColor: "#14c8ff" }}>
                    <Grid.Column>SOLO</Grid.Column>
                  </Grid.Row>
                  {/* <Divider clearing /> */}

                  <Grid.Row columns="5" stretched>
                    <Grid.Column textAlign="center" verticalAlign="middle">
                      <Header size="medium" style={{ color: "white" }}>
                        5.0
                        <Header.Subheader style={{ color: "#92a2bd" }}>
                          K/D
                        </Header.Subheader>
                      </Header>
                    </Grid.Column>
                    <Grid.Column textAlign="center" verticalAlign="middle">
                      <Header size="medium" style={{ color: "white" }}>
                        300
                        <Header.Subheader style={{ color: "#92a2bd" }}>
                          Wins
                        </Header.Subheader>
                      </Header>
                    </Grid.Column>
                    <Grid.Column textAlign="center" verticalAlign="middle">
                      <Header size="medium" style={{ color: "white" }}>
                        2100
                        <Header.Subheader style={{ color: "#92a2bd" }}>
                          Kills
                        </Header.Subheader>
                      </Header>
                    </Grid.Column>
                    <Grid.Column textAlign="center" verticalAlign="middle">
                      <Header size="medium" style={{ color: "white" }}>
                        10.5
                        <Header.Subheader style={{ color: "#92a2bd" }}>
                          Win %
                        </Header.Subheader>
                      </Header>
                    </Grid.Column>
                    <Grid.Column textAlign="center" verticalAlign="middle">
                      <Header size="medium" style={{ color: "white" }}>
                        10000
                        <Header.Subheader style={{ color: "#92a2bd" }}>
                          Matches
                        </Header.Subheader>
                      </Header>
                    </Grid.Column>
                  </Grid.Row>
                </Grid>
                {/* ********************* */}
              </Container>
            </Grid.Column>
          </Grid>
        </Container>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    searchResults: state.search.searchResults
  };
};

const mapDispatchToProps = dispatch =>
  bindActionCreators({ fetchByUsername }, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Leaderboard);
